import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';

const modal = document.getElementById('ask-modal');
if (modal) {
  const form = modal.querySelector('form');
  const questionInput = modal.querySelector('textarea[name="question"]');
  const phoneInput = modal.querySelector('input[name="phone"]');

  form?.addEventListener('submit', async e => {
    e.preventDefault();

    const question = questionInput?.value.trim();
    const phone = phoneInput?.value.trim();

    if (!question) {
      iziToast.error({
        title: 'Error',
        message: 'Please enter a question.',
        position: 'topRight',
      });
      return;
    }

    try {
      const resp = await fetch('https://sweet-chest.onrender.com/ask', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ question, phone }),
      });

      const data = await resp.json();

      if (!resp.ok || !data.success) {
        console.error('Ask error payload:', data);
        throw new Error(data.message || 'Ask error');
      }

      iziToast.success({
        title: 'Sent',
        message: 'Your question was sent üíõ',
        position: 'topRight',
      });
      form.reset();
    } catch (err) {
      console.error('Ask request error:', err);
      iziToast.error({
        title: 'Error',
        message: 'Failed to send. Try later üíî',
        position: 'topRight',
      });
    }
  });
}

