import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';

const modal = document.getElementById('upload-modal'); // —Ç–≤–æ—è –º–æ–¥–∞–ª–∫–∞
if (modal) {
  const fileInput = modal.querySelector('input[type="file"]');
  const phoneInput = modal.querySelector('input[name="phone"]');
  const previewImg = modal.querySelector('.upload-preview'); // img –¥–ª—è –ø—Ä–µ–≤‚Äô—é (–Ω–µ–æ–±–æ–≤‚Äô—è–∑–∫–æ–≤–æ)
  const form = modal.querySelector('form');

  // –ø—Ä–µ–≤‚Äô—é
  if (fileInput && previewImg) {
    fileInput.addEventListener('change', () => {
      const f = fileInput.files?.[0];
      if (!f) return;

      // –º‚Äô—è–∫–µ –æ–±–º–µ–∂–µ–Ω–Ω—è ~8 –ú–±
      const max = 8 * 1024 * 1024;
      if (f.size > max) {
        iziToast.warning({
          title: 'Large file',
          message: 'Please choose an image up to 8 MB.',
          position: 'topRight',
        });
        fileInput.value = '';
        return;
      }

      const url = URL.createObjectURL(f);
      previewImg.src = url;
    });
  }

  form?.addEventListener('submit', async e => {
    e.preventDefault();

    const phone = phoneInput?.value.trim();
    const file = fileInput?.files?.[0];

    if (!file) {
      iziToast.error({
        title: 'Error',
        message: 'Please choose an image.',
        position: 'topRight',
      });
      return;
    }
    if (!phone) {
      iziToast.error({
        title: 'Error',
        message: 'Please enter your phone number.',
        position: 'topRight',
      });
      return;
    }

    const fd = new FormData();
    fd.append('image', file); // ‚ùó –ö–õ–Æ–ß –î–û–õ–ñ–ï–ù –ë–´–¢–¨ 'image'
    fd.append('phone', phone);

    try {
      const resp = await fetch('https://sweet-chest.onrender.com/upload', {
        method: 'POST',
        body: fd, // ‚ùó –ù–µ —Å—Ç–∞–≤–∏–º–æ Content-Type –≤—Ä—É—á–Ω—É
      });

      const data = await resp.json();

      if (!resp.ok || !data.success) {
        console.error('Upload error payload:', data);
        throw new Error(data.message || 'Upload error');
      }

      iziToast.success({
        title: 'Sent',
        message: 'Photo sent to our pastry chef üíõ',
        position: 'topRight',
      });
      form.reset();
      if (previewImg) previewImg.src = '';
    } catch (err) {
      console.error('Upload request error:', err);
      iziToast.error({
        title: 'Error',
        message: 'Failed to upload photo. Try again later üíî',
        position: 'topRight',
      });
    }
  });
}

