import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';

document.addEventListener('DOMContentLoaded', () => {
  const form = document.querySelector('.order-form');
  if (!form) return;

  form.addEventListener('submit', async e => {
    e.preventDefault();

    const phoneInput = form.querySelector('input[name="phone"]');
    const phone = phoneInput?.value.trim();
    const checkbox = document.querySelector('.order-checkbox'); // —è–∫—â–æ —î –∑–≥–æ–¥–∞

    if (!phone) {
      iziToast.error({
        title: 'Error',
        message: 'Please enter your phone number.',
        position: 'topRight',
      });
      return;
    }

    const phoneRegex = /^\+?\d[\d\s\-\(\)]{8,}$/;
    if (!phoneRegex.test(phone)) {
      iziToast.warning({
        title: 'Invalid number',
        message: 'Please enter a valid phone number.',
        position: 'topRight',
      });
      return;
    }

    if (checkbox && !checkbox.checked) {
      iziToast.warning({
        title: 'Consent required',
        message: 'Please agree to personal data processing.',
        position: 'topRight',
      });
      return;
    }

    // –æ–ø—Ü—ñ–æ–Ω–∞–ª—å–Ω–æ –±–µ—Ä–µ–º–æ –Ω–∞–∑–≤—É –ø—Ä–æ–¥—É–∫—Ç–∞, —è–∫—â–æ –≤—ñ–¥–ø—Ä–∞–≤–ª—è—î—à –∑ –∫–∞—Ä—Ç–∫–∏
    const product = form.dataset.product || '';

    try {
      const resp = await fetch('https://sweet-chest.onrender.com/send', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ phone, product }),
      });

      // ‚ùó —Å–µ—Ä–≤–µ—Ä –∑–∞–≤–∂–¥–∏ –ø–æ–≤–µ—Ä—Ç–∞—î JSON –≤–∏–¥—É { success: true/false, ... }
      const data = await resp.json();

      if (!resp.ok || !data.success) {
        console.error('Telegram error payload:', data);
        throw new Error(data.message || 'Telegram error');
      }

      iziToast.success({
        title: 'Success',
        message: 'Your order has been sent! We‚Äôll call you back soon üíõ',
        position: 'topRight',
      });
      form.reset();
      if (checkbox) checkbox.checked = false;
    } catch (err) {
      console.error('Request error:', err);
      iziToast.error({
        title: 'Error',
        message: 'Failed to send message. Try again later üíî',
        position: 'topRight',
      });
    }
  });
});
